___CSS_LOADER_EXPORT___.push([module.id, `@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 222.2 84% 4.9%;
    --card: 0 0% 100%;
    --card-foreground: 222.2 84% 4.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 222.2 84% 4.9%;
    --primary: 198 93% 60%;
    --primary-foreground: 210 20% 98%;
    --secondary: 215 20.2% 65.1%;
    --secondary-foreground: 222.2 47.4% 11.2%;
    --muted: 210 40% 96.1%;
    --muted-foreground: 215.4 16.3% 46.9%;
    --accent: 198 93% 60%;
    --accent-foreground: 222.2 47.4% 11.2%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 210 20% 98%;
    --border: 214.3 31.8% 91.4%;
    --input: 214.3 31.8% 91.4%;
    --ring: 198 93% 60%;
    --radius: 0.5rem;
  }

  .dark {
    --background: 224 71.4% 4.1%;
    --foreground: 210 20% 98%;
    --card: 224 71.4% 4.1%;
    --card-foreground: 210 20% 98%;
    --popover: 224 71.4% 4.1%;
    --popover-foreground: 210 20% 98%;
    --primary: 198 93% 60%;
    --primary-foreground: 210 20% 98%;
    --secondary: 215 27.9% 16.9%;
    --secondary-foreground: 210 20% 98%;
    --muted: 215 27.9% 16.9%;
    --muted-foreground: 217.9 10.6% 64.9%;
    --accent: 215 27.9% 16.9%;
    --accent-foreground: 210 20% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 210 20% 98%;
    --border: 215 27.9% 16.9%;
    --input: 215 27.9% 16.9%;
    --ring: 198 93% 60%;
  }
}

@layer base {
  * {
    @apply border-gray-200 dark:border-gray-700;
  }
  
  body {
    @apply bg-gray-50 text-gray-900 dark:bg-gray-900 dark:text-gray-100;
    font-feature-settings: "rlig" 1, "calt" 1;
  }

  /* Custom scrollbar */
  ::-webkit-scrollbar {
    width: 12px;
    height: 12px;
  }

  ::-webkit-scrollbar-track {
    @apply bg-gray-100 dark:bg-gray-800;
  }

  ::-webkit-scrollbar-thumb {
    @apply bg-gray-300 dark:bg-gray-600 rounded-md;
  }

  ::-webkit-scrollbar-thumb:hover {
    @apply bg-gray-400 dark:bg-gray-500;
  }

  /* Code blocks */
  pre, code {
    @apply font-mono;
  }

  /* Selection */
  ::selection {
    @apply bg-blue-200 text-blue-900 dark:bg-blue-800 dark:text-blue-100;
  }
}

@layer components {
  /* Utility classes for common patterns */
  .container-custom {
    @apply mx-auto px-4 sm:px-6 lg:px-8 max-w-7xl;
  }

  .card-modern {
    @apply bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 shadow-sm hover:shadow-md transition-shadow;
  }

  .btn-primary {
    @apply bg-blue-600 text-white hover:bg-blue-700 px-4 py-2 rounded-md font-medium transition-colors;
  }

  .btn-secondary {
    @apply bg-gray-200 text-gray-900 hover:bg-gray-300 dark:bg-gray-700 dark:text-gray-100 dark:hover:bg-gray-600 px-4 py-2 rounded-md font-medium transition-colors;
  }

  /* Forum-specific styles */
  .forum-card {
    @apply card-modern p-6 mb-4 hover:border-blue-500/50 transition-all;
  }

  .forum-stat {
    @apply text-sm text-gray-600 dark:text-gray-400;
  }

  /* Code editor styles */
  .code-editor-container {
    @apply rounded-lg overflow-hidden border;
  }

  /* Loading states */
  .skeleton {
    @apply animate-pulse bg-gray-200 dark:bg-gray-700 rounded;
  }
}

@layer utilities {
  /* Animation utilities */
  .animate-in {
    animation: fade-in 0.5s ease-in-out;
  }

  .animate-slide-up {
    animation: slide-up 0.3s ease-out;
  }

  /* Text utilities */
  .text-balance {
    text-wrap: balance;
  }

  /* Gradient utilities */
  .gradient-primary {
    @apply bg-gradient-to-r from-blue-500 to-blue-600;
  }

  .gradient-dark {
    @apply bg-gradient-to-br from-gray-800 via-gray-900 to-black;
  }
}`, "",{"version":3,"sources":["webpack://./frontend/src/styles/index.css"],"names":[],"mappings":"AAAA,cAAc;AACd,oBAAoB;AACpB,mBAAmB;;AAEnB;EACE;IACE,uBAAuB;IACvB,4BAA4B;IAC5B,iBAAiB;IACjB,iCAAiC;IACjC,oBAAoB;IACpB,oCAAoC;IACpC,sBAAsB;IACtB,iCAAiC;IACjC,4BAA4B;IAC5B,yCAAyC;IACzC,sBAAsB;IACtB,qCAAqC;IACrC,qBAAqB;IACrB,sCAAsC;IACtC,4BAA4B;IAC5B,qCAAqC;IACrC,2BAA2B;IAC3B,0BAA0B;IAC1B,mBAAmB;IACnB,gBAAgB;EAClB;;EAEA;IACE,4BAA4B;IAC5B,yBAAyB;IACzB,sBAAsB;IACtB,8BAA8B;IAC9B,yBAAyB;IACzB,iCAAiC;IACjC,sBAAsB;IACtB,iCAAiC;IACjC,4BAA4B;IAC5B,mCAAmC;IACnC,wBAAwB;IACxB,qCAAqC;IACrC,yBAAyB;IACzB,gCAAgC;IAChC,4BAA4B;IAC5B,qCAAqC;IACrC,yBAAyB;IACzB,wBAAwB;IACxB,mBAAmB;EACrB;AACF;;AAEA;EACE;IACE,2CAA2C;EAC7C;;EAEA;IACE,mEAAmE;IACnE,yCAAyC;EAC3C;;EAEA,qBAAqB;EACrB;IACE,WAAW;IACX,YAAY;EACd;;EAEA;IACE,mCAAmC;EACrC;;EAEA;IACE,8CAA8C;EAChD;;EAEA;IACE,mCAAmC;EACrC;;EAEA,gBAAgB;EAChB;IACE,gBAAgB;EAClB;;EAEA,cAAc;EACd;IACE,oEAAoE;EACtE;AACF;;AAEA;EACE,wCAAwC;EACxC;IACE,6CAA6C;EAC/C;;EAEA;IACE,mIAAmI;EACrI;;EAEA;IACE,kGAAkG;EACpG;;EAEA;IACE,gKAAgK;EAClK;;EAEA,0BAA0B;EAC1B;IACE,mEAAmE;EACrE;;EAEA;IACE,+CAA+C;EACjD;;EAEA,uBAAuB;EACvB;IACE,wCAAwC;EAC1C;;EAEA,mBAAmB;EACnB;IACE,yDAAyD;EAC3D;AACF;;AAEA;EACE,wBAAwB;EACxB;IACE,mCAAmC;EACrC;;EAEA;IACE,iCAAiC;EACnC;;EAEA,mBAAmB;EACnB;IACE,kBAAkB;EACpB;;EAEA,uBAAuB;EACvB;IACE,iDAAiD;EACnD;;EAEA;IACE,4DAA4D;EAC9D;AACF","sourcesContent":["@tailwind base;\n@tailwind components;\n@tailwind utilities;\n\n@layer base {\n  :root {\n    --background: 0 0% 100%;\n    --foreground: 222.2 84% 4.9%;\n    --card: 0 0% 100%;\n    --card-foreground: 222.2 84% 4.9%;\n    --popover: 0 0% 100%;\n    --popover-foreground: 222.2 84% 4.9%;\n    --primary: 198 93% 60%;\n    --primary-foreground: 210 20% 98%;\n    --secondary: 215 20.2% 65.1%;\n    --secondary-foreground: 222.2 47.4% 11.2%;\n    --muted: 210 40% 96.1%;\n    --muted-foreground: 215.4 16.3% 46.9%;\n    --accent: 198 93% 60%;\n    --accent-foreground: 222.2 47.4% 11.2%;\n    --destructive: 0 84.2% 60.2%;\n   